.dropdown {
	width: 100%;
	font-family: "Montserrat", sans-serif;
	display: flex-wrap;
	background: #f6f6f6;
	border-radius: 5px;
}
.dropdown__toggle {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 19px;
	height: 50px;
	font-size: 24px;
	font-weight: 500;
	line-height: 34px;
	color: #ffffff;
	background: #ff6060;
	border-radius: 5px;
	cursor: pointer;
}
.dropdown__content {
	font-size: 24px;
	font-weight: 400;
	line-height: 34px;
	color: #ff6060;
	text-align: left;
	padding: 18px;
}
.dropdown-wrapper {
	margin: 15px 0;
}
@media screen and (max-width: 809px) {
	.dropdown-wrapper {
	margin: 10px 0;
}
	.dropdown__toggle {
		font-size: 13px;
		line-height: 19px;
	}
	.dropdown__content {
		font-size: 12px;
		line-height: 17px;
	}
}
