.tag {
	font-family: "Montserrat", sans-serif;
	font-size: 14px;
	font-weight: 500;
	line-height: 20px;
	text-align: center;
	background: #ff6060;
	color: #ffffff;
	min-width: 115px;
	height: 25px;
	border-radius: 10px;
	margin: 0 5px;
	padding: 0 5px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	display: flex;
	justify-content: center;
	align-items: center;
}
@media screen and (max-width: 809px) {
	.tag {
		margin-bottom: 5px;
		font-size: 10px;
		line-height: 14px;
    height: 18px;
    border-radius: 5px;
    min-width: 84px;
	}
}
